body {
  background-color: #387ff2;
  margin: 50px;
  font-family: Arial, sans-serif;
}

.container {
  justify-content: center;
  align-items: center;
  display: flex;
  
  align-items: center;
}

.base-image-canvas {
  border: 5px solid white;
  border-radius: 10px;
}

.overlay-options {
  overflow-x: auto;
  max-width: 500px;
  display: flex;
  flex-wrap: nowrap;
  
  margin: 10px;
  border: 5px solid white;
  border-radius: 10px;
  padding: 15px;
}

.overlay-options img {
  width: 100px;
  height: auto;
  cursor: pointer;
}

.overlay-option.selected {
  outline: 5px solid white;
  border-radius: 5px;
}

.overlay-option {
  cursor: pointer;
  margin-left: 20px;
}

.overlay-option.empty {
  cursor: pointer;
  margin: 0px;
}

.overlay-option img {
  height: auto;
  display: block;
}

@media (max-width: 1000px) {
  body {
    margin: 0px;
  }

  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .overlay-options {
    display: flex;
    overflow-x: auto;
    margin-left: 0;
    width: 100%;
    margin: auto;
  }

  .base-image-container img {
    margin: 50px;
    height: auto;
  }

  .base-image-container {
    padding: 20px;
    max-width: 500px;
  }

  .base-image {
    display: flex;
  }

  .overlay-options-container {
    width: 70%;
  }

  .overlay-option {
    margin-left: 0px;
  }
}